.promo {
  padding-bottom: 60px;
}
.promo__body {
  min-height: min-content;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #eaeaea8a;
  border-radius: 10px;
}
.promo__title {
  font-size: clamp(24px, 3vw, 40px);
  font-weight: 600;
  line-height: 40px;
  text-align: center;
}
.promo__feautures {
  text-align: right;
}
.promo__feautures-li {
}
.promo__feautures-link {
}

.label-checkbox {
  text-transform: uppercase;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 10px;
  width: 90px;
  height: 30px;
  border-radius: 50px;
  line-height: 10px;
  color: #31b3ff;
  text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.15);
  background: rgb(71, 71, 71);
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.2), inset 0 0 0 5px rgb(60, 60, 60), inset 0 6px 6px rgba(0, 0, 0, 0.5),
    inset 0 -6px 1px rgba(255, 255, 255, 0.2);
  position: relative;
  &:after {
    background: #ccc linear-gradient(#fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    transition: all 0.5s ease 0s;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 6px;
    content: "";
    width: 48%;
    height: 60%;
    border-radius: 50px;
  }
  &:before {
    content: "OFF";
    position: absolute;
    right: 8px;
  }
}
.big-check {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  &:checked + .label-checkbox::after {
    left: 41px;
  }
}
.big-btn {
  width: 220px;
  height: 40px;
  &:disabled {
    filter: sepia(0.5);
    &:hover {
      background-image: var(--btn-gradient);
      cursor: unset;
    }
  }
}
.input-btn {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.label-btn {
  display: flex;
  width: 40px;
  height: 40px;
  border: 2px solid #cbb8b8;
  background: 0 0;
  box-sizing: border-box;
  border-radius: 50%;
  box-shadow: inset 3px 3px 0px 0px #000, 3px 3px 0 0px #000;
  position: relative;
  &:after {
    position: absolute;
    content: "";
    border-radius: 50%;
    width: 75%;
    height: 75%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: radial-gradient(59.17% 60.83% at 40.83% 39.17%, #fff9f9 0%, rgba(252, 0, 0, 0.71) 100%);
    box-shadow: 2px 3px 4px 1px rgba(0, 0, 0, 0.61);
    filter: sepia(1);
  }
}
.label-btn-on {
  &:after {
    background: radial-gradient(59.17% 60.83% at 40.83% 39.17%, #fff9f9 0%, rgba(79, 213, 84, 0.71) 100%);
  }
}
.input-btn-on {
}
.label-btn-off {
  &:after {
    background: radial-gradient(59.17% 60.83% at 40.83% 39.17%, #fff9f9 0%, rgba(252, 0, 0, 0.71) 100%);
  }
}
.input-btn-off {
}
.input-btn:checked + .label-btn::after {
  filter: sepia(0);
}
